ц, в-третьих, тензор - это базовое понятие всех точных наук.\nМы тоже привыкли работать основательно и являемся профессионалами в каждом отдельном направлении деятельности. Если повесить все наши сертификаты, статусы, лицензии и благодарности на стену, то площадь этой стены составит 20 м2 :)\n\t\t")])])])
